//@import "variables.scss";

/*
//
// General
//
*/

header{
    position:fixed;
}

body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    //color: $primaryColor;
    color: #000;
}

.loading{
    display: flex;
    width:100vw;
    height:100vh;
    align-items: center;
    justify-content: center;

}

.app {
    display: flex;
}

/*
//
// Nav
//
*/

nav{
    margin: 0 0.5vw;
}

nav ul{
    //reset
    //list-style: initial;
    list-style-type: none;
    margin: initial;
    padding: 0 0 0 0px;
    display: flex;

}

nav .button{
    margin-right: 0.5vw;
    
}

//Show current lang
nav [data-current-language="false"]{
    zoom: 1;
    filter: alpha(opacity=60);
    opacity: 0.6;
    background-color: none;
}

.lang .flag-icon{
    background-size: cover;
    background-position: 50%;
    background-repeat: no-repeat;
    width: 40px;
    border-radius: 50%;
    height: 40px;
}


/*
//
// Main
//
*/

#wordContainer{
    margin: 0.5vw;
}

.words{
	display: flex;
	flex-wrap: wrap;
	//justify-content: space-between;

}

.wordBoxCont{
	//flex:1;
	//width:33.33333vw;
    width:32vw;
	min-height:25vh; //enough to show there are more
	display: flex;
	//justify-content: center; /* align horizontal */
	align-items: center; /* align vertical */
	//word-break: break-all;
	//cursor: pointer;
	//background-color:rgba(0,255,0,0.1);
    margin: 0.5vw;

    @media (max-width: 600px) {

        width:100vw;
  
    }

    @media (min-width: 601px) and (max-width: 900px) {
        width:47.5vw;
        flex-grow: 1;
  }

}

.wordBox{
	width:75%;
	text-align: center;
}

.translation{
    visibility: hidden;
}

.showTrans .translation, .wordContent .showTrans{
    visibility: visible;
}

.showTrans .translationButton{ //hide button when all translations shown.
    //visibility: hidden;
}

.showTrans .showTrans.translation{ //Allow toggle button to still work when all translations are shown.
    visibility: hidden;
}

.wordBoxCont .buttons{
    margin-left: auto;
    flex-direction: column;
}

/*
//
// Favourites
//
*/

.noFavouritesCont{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    cursor:pointer;
}

.showFavourites [data-favourite="false"]{
    display:none;
}